@use 'global' as *;

.swiper-slide {
  height: auto;
}
.top-campaign-swiper__swiper-slide {
  height: auto;
  display: flex;
  width: 100%;
  flex-direction: column;
  @include mq('md') {
    // width: 333px;
    max-width: rem(333);
  }
}
.top-campaign-swiper__swiper-slide a {
  height: 100%;
  width: 100%;
  display: block;

  box-shadow: 0px rem(4) rem(4) rgba(0, 0, 0, 0.25);
  @include mq('md') {
  }
}
.top-campaign__swiper-container {
  overflow-x: hidden;
  background-color: transparent;
  margin-top: rem(3);
  margin-right: calc(50% - 50vw);
  width: 100vw;
  height: auto;
  @include mq('md') {
    margin-top: rem(39);
  }
}

.top-campaign-swiper__item {
  height: 100%;
  display: flex;
  flex-direction: column;
  @include mq('md') {
  }
}

