.top-campaign-swiper__img .swiper-slide img,
.top-campaign-swiper__img .swiper-slide picture {
  aspect-ratio: 280 / 188;
  object-fit: cover;
  min-height: initial;
  @include mq('md') {
    aspect-ratio: 333/223;
    min-height: initial;
  }
}
.top-campaign-swiper__img img {
  aspect-ratio: 280 / 188;
  object-fit: cover;
  @include mq('md') {
    aspect-ratio: 333/223;
    min-height: initial;
  }
}
